<template>
  <div class="CheckRoom">

    <van-sticky >

    <van-nav-bar
    title="顷刻"
    left-text="返回"
    :right-text="myid"
    left-arrow
    @click-left="onClickLeft"
    @click-right="onClickRight"/>

  </van-sticky>

    <h1>我的小屋</h1>

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    </van-pull-refresh>

    <van-list
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="onload">

            <van-cell v-for="(item,index) in pictures" :key="item" :value="rent[index]" :title="type[index]" title-style="text-align: left; margin-left: 20px"  clickable @click="pop(item)" :icon="pictures[index]">
                <template #icon>
                    <van-image
                         width="80"
                          height="80"
                          v-bind:src="require('../assets/'+pictures[index])">
                    </van-image>
                </template>
                <template #default>
                  <div v-if="pass">
                    <div style="font-size: 20px; color: #FF0000">{{rent[index]}}</div><div style="color: #FF0000">租约到期</div>
                  </div>
                  <div v-if="notpass">
                    <div style="font-size: 20px;color: #FF0000">待审核</div>
                  </div>
                </template>
                <template #label>
                    <div v-bind:style="{width: commentWidth+'px'}"><div class="van-ellipsis">{{address[index]}}</div></div>
                </template>
            </van-cell>
    </van-list>

    <p><van-cell  size="large" title="查询本月需交费用" is-link arrow-direction="down" @click="check"/></p>
    <div v-if="v1">
        <van-cell v-for="(item,index) in paylist" :key="item" clickable @click="clicktopay(item)">
            <span style="font-size:25px;">{{paylist[index]}}</span>
        </van-cell>
    </div>

    <div v-if="v2">
        没有费用需要交。
    </div>

    

  </div>
</template>

<script>
import Foot_bar from './Foot_bar';
import { Toast } from 'vant';
import axios from 'axios';

export default {
  name: 'CheckRoom',
  data () {
    return {
      msg1: '距离租约到期还有：',
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      days:'100',
      msg2:'本月应交',
      pass:true,
      isLoading: false,
      notpass:false,
      paylist:[
          '1',
          '2',
      ],
      v1:false,
      v2:false,
      pictures: ['qk1000101.jpg','qk1000201.jpg'],
      type: ['1','2'],
      rent: ['2020年12月1日','2021年12月1日'],
      address: ['1','2'],
	  state:[],
    }
  },
  components:{
      Foot_bar
  },
  methods:{
      onRefresh() {
          setTimeout(() => {
              Toast('刷新成功');
              this.isLoading = false;
           }, 1000);
      },
      judge(){
          if(this.myid=="请先登入"){
              this.islogin="/";
          }
      },
      onload(){
          if(this.type.length==this.rent.length){
              this.finished=true;
          }
      },
      check(){
          if(this.paylist.length==0){
              this.v1=false;
              this.v2=true;
          }
          else{
              this.v1=true;
              this.v2=false;
          }
      },
      onClickLeft() {
          this.$router.push({
            path:'/MyInfo',
          });
      },
      onClickRight() {
          this.$router.push({
            path:'/MyInfo',
          });
    },
    clicktopay(k){
        this.$router.push({
            path:'/Paying',
            query:{money:k},
        });
    }
  },
  mounted(){
	var _this = this;
	axios.get('/showrenterroom',{
		params:{
			user:_this.myid,
		}
	})
	.then(function(response){
		concole.log(response.data.pictures);
		_this.pictures = response.data.pictures;
		_this.type = response.data.type;
		_this.rent = response.data.date;
		_this.state = response.data.state;
		_this.address = response.data.address;
	})
	.catch(function(error){
	});
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



